import React, { Component, Fragment } from 'react';
import { Switch, Route, withRouter } from 'react-router-dom';

// ui components
import InstallIcon from 'material-ui/svg-icons/action/settings-applications';
import ArrowDropRight from 'material-ui/svg-icons/navigation-arrow-drop-right';
import MenuIcon from 'material-ui/svg-icons/navigation/menu';
import BuildIcon from 'material-ui/svg-icons/action/build';
import CodeIcon from 'material-ui/svg-icons/action/code';
import HomeIcon from 'material-ui/svg-icons/action/home';
import InfoIcon from 'material-ui/svg-icons/action/info';
import SaveIcon from 'material-ui/svg-icons/content/save';
import AuthorIcon from 'material-ui/svg-icons/social/group';
import { Drawer, IconButton, Menu, MenuItem } from 'material-ui';

import 'highlight.js/styles/solarized-dark.css';
import './App.css';

import Home from './components/Home';
import AMD from './components/Install/AMD';

class App extends Component {
	state = {
		openDrawer: false
	};
	render() {
		return (
			<Fragment>
				<IconButton onClick={() => this.setState({ openDrawer: true })}>
					<MenuIcon />
				</IconButton>
				<Drawer
					docked={false}
					open={this.state.openDrawer}
					onRequestChange={(openDrawer) => this.setState({ openDrawer })}
				>
					<Menu desktop={true}>
						<MenuItem
							primaryText="Inicio"
							leftIcon={<HomeIcon />}
							onClick={() => {
								this.props.history.push('/');
								this.setState({ openDrawer: false });
							}}
						/>
						<MenuItem
							primaryText="Instalación"
							leftIcon={<InstallIcon />}
							rightIcon={<ArrowDropRight />}
							menuItems={[
								<MenuItem
									primaryText="AMD"
									key="AMD"
									onClick={() => {
										this.props.history.push('/install/amd');
										this.setState({ openDrawer: false });
									}}
								/>,
								<MenuItem
									primaryText="INTEL"
									key="INTEL"
									onClick={() => {
										this.setState({ openDrawer: false });
									}}
								/>
							]}
						/>
						<MenuItem primaryText="Acerca de OpenCL" leftIcon={<InfoIcon />} />
						<MenuItem
							primaryText="Modelo de ejecución"
							leftIcon={<BuildIcon />}
						/>
						<MenuItem primaryText="Tutorial" leftIcon={<CodeIcon />} />
						<MenuItem primaryText="Ejemplos" leftIcon={<SaveIcon />} />
						<MenuItem primaryText="Autores" leftIcon={<AuthorIcon />} />
					</Menu>
				</Drawer>
				<Switch>
					<Route exact path="/" component={Home} />
					<Route exact path="/install/amd" component={AMD} />
				</Switch>
			</Fragment>
		);
	}
}

export default withRouter(App);



