import React, { Fragment, Component } from 'react';

import {
	FlatButton,
	IconButton,
	Paper,
	RaisedButton,
	Step,
	StepLabel,
	Stepper
} from 'material-ui';

import LinkIcon from 'material-ui/svg-icons/content/link';
import ArrowForward from 'material-ui/svg-icons/navigation/arrow-forward';

import logoAMD from '../../images/LogoAMD.png';

const ArrowStyle = { position: 'relative', top: '5px' };

class AMD extends Component {
	state = { stepIndex: 0, finished: false };
	handleNext() {
		const { stepIndex } = this.state;
		this.setState({
			stepIndex: stepIndex + 1,
			finished: stepIndex >= 3
		});
	}

	handlePrev() {
		const { stepIndex } = this.state;
		if (stepIndex > 0) {
			this.setState({ stepIndex: stepIndex - 1 });
		}
	}
	getContent(index) {
		switch (index) {
			case 0:
				return (
					<Paper className="col">
						<h1>Descarga del SDK</h1>
						<div className="col">
							Para compilar el programa Host, es importante bajar la última
							versión de AMD APP SDK, que proporciona los headers para runtime y
							las librerías necesarias. Dentro del siguiente mensage de foro se
							puede encontrar los links para Windows.
						</div>
						<div className="col row justify-content-center">
							<IconButton
								className="align-self-center"
								href="https://community.amd.com/message/2739800"
								iconStyle={{
									width: 48,
									height: 48
								}}
								style={{
									width: 96,
									height: 96,
									padding: 20
								}}
								tooltip="OpenCL AMD SDK"
								tooltipPosition="bottom-right"
							>
								<LinkIcon />
							</IconButton>
						</div>
						<div className="col">
							Adicionalmente se agrega la ruta donde se instalo el SDK a la
							variable AMDAPPSDKROOT y esta se agrega a PATH.
						</div>
					</Paper>
				);
			case 1:
				return (
					<Paper className="col">
						<h1>Compilando en Windows</h1>
						<span>
							Project Properties<ArrowForward style={ArrowStyle} />
							C/C++<ArrowForward style={ArrowStyle} />
							Additional Include Directories<ArrowForward style={ArrowStyle} />
							$(AMDAPPSDKROOT)/include
						</span>
						<div className="col-md-7">
							<img
								className="col"
								src="https://vasanthexperiments.files.wordpress.com/2011/07/installation3.png"
								alt="installation-1"
							/>
						</div>
					</Paper>
				);
			case 2:
				return (
					<Paper className="col">
						<h1>Librerías</h1>
						<span>
							Project Properties<ArrowForward style={ArrowStyle} />
							Linker<ArrowForward style={ArrowStyle} />
							Additional Library Directories
						</span>
						<div className="ws-100" />
						<span>
							Esto debe incluir $(AMDAPPSDKROOT)/lib/x86 para librerías OpenCL
							de 32-bit y $(AMDAPPSDKROOT)/lib/x86_64 para 64-bit.
						</span>
						<div className="col-md-7">
							<img
								className="col"
								src="https://vasanthexperiments.files.wordpress.com/2011/07/installation-6.png"
								alt="installation-2"
							/>
						</div>
					</Paper>
				);
			case 3:
				return (
					<Paper className="col">
						<h1>Dependencias</h1>
						<span>
							Project Properties<ArrowForward style={ArrowStyle} />
							Linker<ArrowForward style={ArrowStyle} />
							Input<ArrowForward style={ArrowStyle} />
							Additional Dependencies
						</span>
						<div className="w-100" />
						<span>Estas deben incluir OpenCL.lib</span>
						<div className="col-md-7">
							<img
								className="col"
								src="https://vasanthexperiments.files.wordpress.com/2011/07/install-8.png"
								alt="installation-2"
							/>
						</div>
					</Paper>
				);
			default:
				return null;
		}
	}
	render() {
		const { stepIndex, finished } = this.state;
		return (
			<Fragment>
				<div className="row justify-content-center m-md-5">
					<img src={logoAMD} alt="AMD" />
				</div>
				<Paper className="row m-md-5">
					<div className="col">
						<Stepper activeStep={stepIndex}>
							<Step>
								<StepLabel>Descarga</StepLabel>
							</Step>
							<Step>
								<StepLabel>Incluir directorios</StepLabel>
							</Step>
							<Step>
								<StepLabel>Librerías</StepLabel>
							</Step>
							<Step>
								<StepLabel>Dependencias</StepLabel>
							</Step>
						</Stepper>
						{finished ? (
							<div className="col-md-7">
								<img
									className="col"
									src="http://zonacero.com/sites/default/files//styles/1260x720/public/2016/4/27/foto_detalle/homerosimpson.jpg"
									alt="Finish"
								/>
								<p className="col">
									<RaisedButton
										onClick={(event) => {
											event.preventDefault();
											this.setState({ stepIndex: 0, finished: false });
										}}
									>
										Oprime acá para volver al primer paso
									</RaisedButton>
								</p>
							</div>
						) : (
							<div>
								{this.getContent(stepIndex)}
								<div style={{ marginTop: 12 }}>
									<FlatButton
										label="Anterior"
										disabled={stepIndex === 0}
										onClick={this.handlePrev.bind(this)}
										style={{ marginRight: 12 }}
									/>
									<RaisedButton
										label={stepIndex === 3 ? 'Terminar' : 'Siguiente'}
										primary={true}
										onClick={this.handleNext.bind(this)}
									/>
								</div>
							</div>
						)}
					</div>
				</Paper>
			</Fragment>
		);
	}
}

export default AMD;



