import React, { Fragment } from 'react';
import { Divider, GridList, GridTile, Paper } from 'material-ui';

import logoOpenCL from '../images/LogoOpenCL.png';
import logoKhronos from '../images/LogoKhronos.svg';
import logoApple from '../images/LogoApple.png';
import logoAMD from '../images/LogoAMD.png';
import logoIntel from '../images/LogoIntel.png';
import logoIBM from '../images/LogoIBM.png';
import logoNVIDIA from '../images/LogoNVIDIA.png';

const logos = [
	{ name: 'Apple', img: logoApple, url: 'https://www.apple.com/' },
	{ name: 'AMD', img: logoAMD, url: 'https://www.amd.com/' },
	{ name: 'Intel', img: logoIntel, url: 'https://www.intel.com' },
	{ name: 'IBM', img: logoIBM, url: 'https://www.ibm.com' },
	{ name: 'NVIDIA', img: logoNVIDIA, url: 'http://www.nvidia.com/' }
];

export default () => (
	<Fragment>
		<Paper className="row justify-content-center m-md-5">
			<a href="https://www.khronos.org/opencl/">
				<img src={logoOpenCL} alt="LogoOpenCL" />
			</a>
			<div className="w-100" />
			<h2>
				<b>O</b>pen <b>C</b>omputing <b>L</b>anguage
			</h2>
		</Paper>
		<Divider />
		<div className="row m-md-5">
			<ul className="list-group col-md-8">
				<li className="list-group-item">Api abierta</li>
				<li className="list-group-item">
					Permite a la GPU, DSP y coprocesadores trabajar en conjunto con la CPU
				</li>
				<li className="list-group-item">
					Lanzado en diciembre de 2008 por el grupo Khronos
				</li>
			</ul>
			<a href="https://www.khronos.org/" className="col align-self-center">
				<img src={logoKhronos} alt="LogoKhronos" />
			</a>
		</div>
		<Divider />
		<Paper className="row justify-content-center m-md-5">
			<h2>Desarrollada por</h2>
			<div className="w-100" />
			<GridList className="col-md-6">
				{logos.map(({ name, img, url }) => (
					<GridTile key={name} title={name}>
						<a href={url}>
							<img src={img} alt={name} />
						</a>
					</GridTile>
				))}
			</GridList>
		</Paper>
	</Fragment>
);



